import { documentToReactComponents } from "@contentful/rich-text-react-renderer";
import { BLOCKS } from "@contentful/rich-text-types";
import { graphql } from "gatsby";
import React from "react";

import {
  BlockTitle,
  CenteredRow,
  ColumnTitle,
  FullBlock,
  TableCellDiv,
  TableDiv,
  TableRowDiv,
} from "../../components/content";
import { Main } from "../../components/main";

export const Head = ({ data: { contentfulBlogPost } }) => (
  <title>Kyle Watson - Blog - {contentfulBlogPost.title}</title>
);

export default ({ data: { contentfulBlogPost } }) => {
  return (
    <Main>
      <CenteredRow>
        <FullBlock>
          <BlockTitle>{contentfulBlogPost.title}</BlockTitle>
          {documentToReactComponents(
            JSON.parse(contentfulBlogPost.mainText.raw),
            {
              renderNode: {
                [BLOCKS.HEADING_1]: (_, children) => (
                  <BlockTitle>{children}</BlockTitle>
                ),
                [BLOCKS.HEADING_2]: (_, children) => (
                  <ColumnTitle>{children}</ColumnTitle>
                ),
                [BLOCKS.TABLE]: (_, children) => (
                  <TableDiv>{children}</TableDiv>
                ),
                [BLOCKS.TABLE_ROW]: (_, children) => (
                  <TableRowDiv>{children}</TableRowDiv>
                ),
                [BLOCKS.TABLE_CELL]: (_, children) => (
                  <TableCellDiv>{children}</TableCellDiv>
                ),
              },
            }
          )}
        </FullBlock>
      </CenteredRow>
    </Main>
  );
};

export const data = graphql`
  query pageQuery($id: String!) {
    contentfulBlogPost(id: { eq: $id }) {
      mainText {
        raw
      }
      title
    }
  }
`;
